.radio {
  user-select: none;
}

.radio > [type="radio"] {
  display: inline-block;
  position: relative;
  z-index: 1;
  width: 1.5em;
  height: 1.5em;
  top: 0.25em;
  padding: 0;
  margin: 0;
  visibility: hidden;
}

.radio > [type="radio"]:before {
  visibility: visible;
  content: '';
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  background-color: white;
  border: 2px solid #dbdbdb;
  transition: all 0.25s ease-out;
}

.radio > [type="radio"]:after {
  visibility: visible;
  display: none;
  content: '';
  position: absolute;
  left: 25%;
  top: 25%;
  border-radius: 100%;
  height: 50%;
  width: 50%;
  background-color: #363636;
}

.radio > [type="radio"]:checked:before {
  border-color: #00d1b2;
}

.radio > [type="radio"]:checked:after {
  display: block;
}

.radio:hover > [type="radio"]:not(:checked):before {
  border-color: rgba(0, 0, 0, 0.5);
}

.radio.is-white > [type="checkbox"]:checked:before {
  border-color: #00d1b2;
}

.radio.is-black > [type="checkbox"]:checked:before {
  border-color: #00d1b2;
}

.radio.is-light > [type="checkbox"]:checked:before {
  border-color: #00d1b2;
}

.radio.is-dark > [type="checkbox"]:checked:before {
  border-color: #00d1b2;
}

.radio.is-primary > [type="checkbox"]:checked:before {
  border-color: #00d1b2;
}

.radio.is-link > [type="checkbox"]:checked:before {
  border-color: #00d1b2;
}

.radio.is-info > [type="checkbox"]:checked:before {
  border-color: #00d1b2;
}

.radio.is-success > [type="checkbox"]:checked:before {
  border-color: #00d1b2;
}

.radio.is-warning > [type="checkbox"]:checked:before {
  border-color: #00d1b2;
}

.radio.is-danger > [type="checkbox"]:checked:before {
  border-color: #00d1b2;
}
